import { Component, OnInit } from '@angular/core';
import { fade_1, fade_2, fade_3 } from '../../animations';

@Component({
  selector: 'app-myportfolio',
  templateUrl: './myportfolio.component.html',
  styleUrls: ['./myportfolio.component.css'],
  animations:[
    fade_1,
    fade_2,
    fade_3,
  ]
})
export class MyportfolioComponent implements OnInit {
  menuLists:any[];
  profilepic:string = 'assets/img/prof-pic.jpg';
  education:string = 'assets/img/education1.jpg';
  experience:string = 'assets/img/experience1.jpg';
  skills:string = 'assets/img/desktop-mobile.jpg';
  bar:string = 'assets/img/skills/menu.png';
  contact:string = 'assets/img/skills/smartphone-call.png';
  award:string = 'assets/img/award.jpg';
  html:string = 'assets/img/skills/HTML_Logo.png';
  css:string = 'assets/img/skills/CSS3.png';

  // ANIMATION
  fade_1: string = 'fade_1';
  view_experience: string = "fade_1";
  

  constructor() { }

  ngOnInit() {
    this.menuLists = [
    'Home',
    'About',
    'Project',
    'Contact'
  ];  

  // ANIMATION
  this.animateMe();
  
  }

  animateMe() {
        this.fade_1 = (this.fade_1 === 'small' ? 'large' : 'small');
  }
  
}