import { trigger, transition, state, style, animate } from '@angular/animations';

export let fade_1 =   trigger('fade',[
    transition('small => *',[
      style({opacity: 0, transform: 'translateY(-50%)'}),
      animate(500)
    ]),
    transition('large => *',[
      style({opacity: 0, transform: 'translateY(-50%)'}),
      animate(500)
    ])
  ]);

  export let fade_2 = trigger('fade2',[
    transition('small => *',[
      style({opacity: 0, transform: 'translateY(-50%)'}),
      animate(600)
    ]),
    transition('large => *',[
      style({opacity: 0, transform: 'translateY(-50%)'}),
      animate(500)
    ]),
    transition('large => *',[
      style({opacity: 0, transform: 'translateY(-50%)'}),
      animate(500)
    ])
  ]);

  export let fade_3 =   trigger('fade3',[
    transition('small => *',[
      style({opacity: 0, transform: 'translateY(-50%)'}),
      animate(700)
    ]),
    transition('large => *',[
      style({opacity: 0, transform: 'translateY(-50%)'}),
      animate(500)
    ])
  ]);
